
import ImageService from '../image-service';
import { Options, Vue } from 'vue-class-component';
import { Swiper, SwiperSlide } from 'swiper/vue';

@Options({
   components: { Swiper, SwiperSlide },
})
export default class HelloWorld extends Vue {
   service = new ImageService();
   list = this.service.getAll(); //.slice(0, 2);
   step = 0;
   score = [];
   swiperRef: any = {};
   inChange = false;
   progress = 0;

   created() {
      this.reset();
      setInterval(() => this.sliderMove(), 10);
   }

   reset() {
      this.step = 0;
   }

   setSwiperRef(swiper: any) {
      this.swiperRef = swiper;
      this.swiperRef.activeIndex = 1;
   }

   slide(index: number) {
      /*const animationStep = 10;
      const animationTime = 500;
      const direction = index - 1;
      let tempProgress = 0;
      const update = setInterval(() => {
         tempProgress += animationStep * 2;
         this.progress = (direction * tempProgress) / animationTime;
         if (tempProgress >= animationTime) {
            window.clearInterval(update);
            this.onSlideChange();
         }
      }, animationStep);*/
      this.swiperRef.slideTo(index, 500, false);
      setTimeout(() => this.onSlideChange(), 200);
   }

   onSlideChange() {
      if (this.inChange) return;
      this.inChange = true;
      this.step++;
      this.swiperRef.slideTo(1, 0, false);
      this.progress = 0;
      this.inChange = false;
   }

   sliderMove() {
      this.progress = this.swiperRef.progress * 2 - 1;
   }

   beforeTransitionStart() {
      /*const delta = this.swiperRef.progress - this.progress;
      let i = 0;
      const update = setInterval(() => {
         this.progress += delta / 5;
         if (i++ >= 5) {
            window.clearInterval(update);
            this.onSlideChange();
         }
      }, 100);*/
   }

   degree() {
      return this.progress * 20;
   }

   abs(): number {
      return Math.abs(this.progress);
   }

   percent(): number {
      return this.abs() > 1 ? this.abs() : 30 - this.abs() * 30;
   }

   slideResetTransitionStart() {
      this.progress = 0;
   }
}
